.pageAbout {
    min-height: 100%;
    padding: 40px 3%;

    &__content {
        min-height: 100%;
        padding: 20px 1.5%;
        display: flex;
        flex-direction: column;
        gap: 20px;
        border-radius: 5px;
        color: rgb(7, 49, 126);
        font-weight: 600;
        background: rgba(255, 247, 231, 0.65);
        box-shadow: 0 0 25px 25px rgba(255, 247, 231, 0.65);

        .pageAbout__header {
            display: flex;
            justify-content: center;
            gap: 5%;
            row-gap: 20px;
            align-items: center;
            flex-wrap: wrap;

            &_headline {
                display: inline-block;
                width: fit-content;
                color: #f0ab2c;
                padding: 0px 5% 5px;
                margin: 6px 0 18px;
                text-align: center;
                letter-spacing: 1px;
                font-size: 36px;
                font-weight: 900;
                line-height: 1.05em;
                text-shadow: 0 3px 7px rgba(0, 0, 0, 0.7), 0 0 3px rgba(0, 0, 0, 1),
                    0px 1px 1px rgba(0, 0, 0, 1),
                    1px 0px 1px rgba(0, 0, 0, 1),
                    0px 0px 1px rgba(0, 0, 0, 1),
                    1px 1px 1px rgba(0, 0, 0, 1), ;
                background: rgba(255, 255, 255, 0.35);
                box-shadow: 0 0 20px 20px rgba(255, 255, 255, 0.35);
                border-radius: 160px;

                @supports (-webkit-text-stroke: 1.6px rgb(10, 47, 114)) {
                    -webkit-text-stroke: 1.6px rgb(10, 47, 114);
                    -webkit-text-fill-color: rgb(233, 174, 24);
                    text-shadow: 0 2px 6px rgba(0, 0, 0, 0.65);
                }
            }

            &_foto {
                width: 260px;
                height: 260px;
                box-shadow: 3px 4px 20px 0 rgba(0, 0, 0, 0.6);
                line-height: 0;
                border-radius: 50%;
                overflow: hidden;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    object-position: center;
                }
            }
        }

        .text {
            margin-bottom: 36px;
            font-size: 27px;
            text-align: center;
            padding: 10px 1%;
            font-weight: 900;
            color: #07317e;
            line-height: 1.2em;
        }

        .article {
            border-radius: 30px;
            padding: 20px 3.8% 35px;
            background: rgb(238, 219, 172);
            box-shadow: 0 8px 18px 0 rgba(0, 0, 0, 0.2), inset 0 0 35px 4px rgba(187, 135, 4, 0.65);
            font-size: 18px;
            color: rgb(3, 34, 119);
            display: flex;
            flex-direction: column;

            h2 {
                margin-bottom: 30px;
                font-size: 26px;
                text-align: center;
                color: #003496;
                font-weight: bold;
            }

            &_howICame{
                p {
                    text-indent: 30px;
                    margin-bottom: 6px;
    
                    &:last-of-type {
                        margin-bottom: 0;
                    }
                }
            }

            .skills {
                text-indent: initial;

                .highlightedRed {
                    color: rgb(207, 0, 79);
                    line-height: 1.5em;
                }

                .skills_icon {
                    margin-right: 0.2em;
                    width: 1.5em;
                    position: relative;
                    bottom: calc(0.5em - 1.5em/2);
                }

                .skills_item {
                    position: relative;
                    margin-left: 1.6em;
                    display: inline-block;
                    line-height: 1.7em;

                    &::before {
                        content: "";
                        display: inline-block;
                        width: 1.6em;
                        height: 1.6em;
                        background-image: var(--bgImage);
                        background-position: center center;
                        background-repeat: no-repeat;
                        background-size: contain;
                        position: absolute;
                        left: calc(0em - 1.6em);
                        bottom: calc((1.7em - 1.5em)/2);
                    }
                }
            }


            &__downloadCV {
                display: inline-block;
                padding: 3px 5px;
                border: 2px solid #264580;
                width: max-content;
                align-self: center;
                background: rgb(255, 234, 113);
                color: #264580;
                font-weight: 600;
                transition: 0.1s;
                box-shadow: 0 9px 5px -4px rgba(0, 0, 0, 0.3);
                animation: buttonattention 4s linear infinite;

                &:hover {
                    background: #264580;
                    color: rgb(255, 234, 113);
                }

                @keyframes buttonattention {
                    0% {
                        transform: none;
                        box-shadow: 0 9px 5px -4px rgba(0, 0, 0, 0.3);
                    }

                    2% {
                        transform: translate(-7px, -2px) scale(1.1);
                    }

                    4% {
                        transform: translate(7px, -4px) scale(1.15);
                        box-shadow: 0 11px 10px -2px rgba(0, 0, 0, 0.3);
                    }

                    6% {
                        transform: translate(-5px, -3px) scale(1.15);
                        box-shadow: 0 11px 10px -2px rgba(0, 0, 0, 0.3);
                    }

                    8% {
                        transform: translate(3px, -1px) scale(1.1);
                    }

                    10% {
                        transform: translate(-2px, 0px) scale(1.05);
                    }

                    12% {
                        transform: none;
                        box-shadow: 0 9px 5px -4px rgba(0, 0, 0, 0.3);
                    }
                }
            }
        }
    }
}